.normalKeyLists{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5px;
}

.normalKeyLists li{
  -webkit-flex: 0 0 70px;
          flex: 0 0 70px;
  height: 45px;
  line-height: 45px;
  margin: 4px 0;
  text-align: center;
  border: 1px solid #d1d1d1;
}

.normalKeyLists li:hover{
  background: #fff;
  cursor: pointer;
}


.panel {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px 15px;
  height: 120px;
  width: 100%;
  background: #d1d1d1;
}

.container {
  position: relative;
}

.arrow {
  position: absolute;
  top: 21px;
  height: 8px;
  width:  8px;
  border: 1px solid #4fa50a;
  border-bottom: transparent;
  border-right: transparent;
  display:none;
  cursor: pointer;
}



.arrow.active{
  display: block;
}

.arrow.left{
  left: -8px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.arrow.right{
  right: -8px;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

.panel .text-wrap {
  position: relative;
  height: 50px;
  line-height: 50px;
  width:100%;
  padding: 5px 0;
  text-align: right;
  overflow: hidden;
}

.text-wrap .child {
  position: absolute;
  right :0;
  bottom:0;
  white-space: nowrap;
}

.text-wrap .expression {
  font-size: 18px;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.text-wrap .inputValue {
  font-size: 35px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul li{
  list-style: none;
}

a{
  text-decoration: none;
  color: #000;
}

.App {
  width: 300px;
  min-height: 450px;
  margin: 40px auto;
  color:#1d8ced;
  border: .1px solid #a8d1f5;
  background: #f1f1f1;
}

.App > header {
  height: 30px;
  line-height: 30px;
  text-align: center ;
  font-size: 14px;
}

.App > .mode {
  height: 30px;
  line-height: 30px;
  padding: 0 5px;
  font-size: 14px;
}

.mode .cmode {
  padding: 4px 5px;
  border: none;
  outline: none;
  border-radius:  4px;
  background: none;
}

.cmode:hover{
  background: #fff;
}

.cmode.active {
  font-weight: bold;
}


